/* input */
input[type="text"],
input[type="email"],
input[type="tel"],
input[type="number"],
input[type="password"],
select,
textarea {
  position: relative;
  background-color: #FBFBFB;
  border: 1px solid #888888;
  box-sizing: border-box;
  padding: 0.8rem 0.8rem;
  width: 100%;
  font-family: "Yu Gothic", sans-serif;
  font-weight: 500;
  font-size: 1.6rem;
  line-height: 2.4rem;
  text-align: left;
  letter-spacing: 0.08em;
  color: #303030;
}

input[type="text"],
input[type="email"],
input[type="tel"],
input[type="number"],
input[type="password"],
select {
  height: 4.0rem;
}

input[type="text"]:focus,
input[type="email"]:focus,
input[type="tel"]:focus,
input[type="number"]:focus,
input[type="password"]:focus,
select:focus,
textarea:focus {
  border-color: #888888;
}

input[type="text"]::placeholder,
input[type="email"]::placeholder,
input[type="tel"]::placeholder,
input[type="number"]::placeholder,
input[type="password"]::placeholder,
select::placeholder,
textarea::placeholder {
  color: #AEAEAE;
  font-size: 1.6rem;
}

select {
  position: relative;
  -webkit-appearance: none;
  -moz-appearance: none;
  background-image: url(../img/select-down.png);
  background-position: calc(100% - 0.8rem) 50%;
  background-size: 1.6rem 1.6rem;
  background-repeat: no-repeat;
  z-index: 0;
}

/* checkbox */
.checkbox {
  display: block;
  position: relative;
  padding-left: 3.2rem;
  cursor: pointer;
  font-weight: 400;
  font-size: 1.8rem;
  line-height: 3.0rem;
  text-align: left;
  color: #000000;
  user-select: none;
}

@media only screen and (max-width: 768px) {
  .checkbox {
    font-size: 1.6rem;
    line-height: 2.8rem;
    padding-left: 3.0rem;
  }
}

.checkbox input {
  position: absolute;
  opacity: 0;
  cursor: pointer;
  height: 0;
  width: 0;
}

.checkbox .checkmark {
  position: absolute;
  top: 0.3rem;
  left: 0;
  height: 2.4rem;
  width: 2.4rem;
  background-color: #FFFFFF;
  border-radius: 0.4rem;
  border: 2px solid #1B1B1F;
}

@media only screen and (max-width: 768px) {
  .checkbox .checkmark {
    top: 0.4rem;
    width: 2.0rem;
    height: 2.0rem;
    border-radius: 0.4rem;
  }
}

.checkbox input:focus ~ .checkmark {
  outline: 0.3rem solid #7EBF41;
}

.checkbox input:checked ~ .checkmark {
  background-color: #3B8E65;
  border-color: #3B8E65;
  outline: none;
}

.checkbox .checkmark:after {
  content: "";
  position: absolute;
  display: none;
}

.checkbox input:checked ~ .checkmark:after {
  display: block;
}

.checkbox .checkmark:after {
  left: 0.5rem;
  top: 0;
  width: 0.9rem;
  height: 1.5rem;
  border: solid #FFFFFF;
  border-width: 0 2px 2px 0;
  transform: rotate(45deg);
}

@media only screen and (max-width: 768px) {
  .checkbox .checkmark:after {
    left: 0.4rem;
    top: 0;
    width: 0.8rem;
    height: 1.2rem;
  }
}

picture,
figure,
.image_wrap {
  position: relative;
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  justify-content: center;
  overflow: hidden;
}

picture img,
figure img,
.image_wrap img {
